<template>
  <div>
    <p class="mb-0 text-left"></p>
    <select class="custom-select" v-model="fill">
      <option :value="null"> </option>
      <option :value="f.type" v-for="f in fills" :key="f.type">{{ f.title }}</option>
      <!-- <option value="dots"></option>
      <option value="verticalLines"> </option>
      <option value="horizontalLines"> </option>
      <option value="rightDiagonalLines"> </option>
      <option value="leftDiagonalLines"> </option>
      <option value="rightSmallDiagonalLines"> </option>
      <option value="rhombus"></option> -->
    </select>
  </div>
</template>

<script>
export default {
  data() {
    return {
      fill: null,
    };
  },
  computed: {
    selectedEl() { return this.$store.getters.selectedEl; },
    fills() { return this.$store.state.fills; },
  },
  watch: {
    fill(fill) {
      this.$store.commit('setPartFill', {
        fill,
        i: this.selectedEl.i,
      });
      this.$store.commit('addLog');
    },
    selectedEl(selectedEl) {
      if (selectedEl.el.fill !== this.fill) this.fill = this.selectedEl.el.fill;
    },
  },
  mounted() {
    this.fill = this.selectedEl.el.fill;
  },
};
</script>
